@immport "~bootstrap/scss/bootstrap"

html 
	scroll-behavior: smooth 

.masthead 
	height: 100vh 
	min-height: 500px 
	background-image: url('https://source.unsplash.com/BtbjCFUvBXs/1920x1080') 
	background-size: cover 
	background-position: center 
	background-repeat: no-repeat 

.top 
	position: absolute 
	top: 0 
	right: 0 
	left: 0 
	z-index: 1030 

.navbar-brand 
	color: red 

.page-content-1 
	background-color: #282828 !important 
	color: #E3E2DF 

.page-content-2 
	background-color: #C2B9b0 
	color: #0B0C10 

.food-image 
	max-width: 100% 
	max-height: 150px 
	border-radius: 4px 

.food-franchise-delivery 
	color: #C2B9b0 
	font-size: 12px 

.restaurant-label 
	line-height: 1 
	padding: 10px 5px 

.no-style-link 
	color: inherit 

	&:hover 
		text-decoration: none 
		color: inherit 
